.layout {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.content {
  height: 90%;
  margin-top: 0.5%;
  display: flex;
  justify-content: space-between;
  overflow: auto;
}

.main {
  width: 100%;
  margin: 0 1%;
  overflow: hidden;
  background-color: whitesmoke;
  color: black;
  border-radius: 8px 8px 0 0;
  padding: 0.25%;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.25);
}

.ctn-btn {
  width: 1.25rem;
  margin-bottom: 0.5%;
}

.btn-menu {
  width: 100%;
  height: 100%;
  background-color: #a4161a;
  color: whitesmoke;
  border: none;
}

.rotate {
  transform: rotate(180deg);
}

.btn-menu:hover {
  cursor: pointer;
  background-color: #660708;
}
